import { View, Text } from 'react-native';
import { StackScreenProps } from '@react-navigation/stack';
import { StackParams } from '../../types/stack.params';
import { HomeStyles as styles } from './styles';
import { HomeHeader } from '../../components/headers/home';
import { Container } from '../../components/container';
import { NavigationButton } from '../../components/buttons/navigation';
import { StatusBar } from 'expo-status-bar';
import { ProgressBar } from '../../components/progress_bar';
import { Divider } from '../../components/divider';
import { useContext, useEffect, useState } from 'react';
import { Context } from '../../context';
import { useMetaService } from '../../services/meta.service';
import { Meta } from '../../models/meta.model';

interface Properties extends StackScreenProps<StackParams, 'Home'> {}

export default function Home({ navigation }: Properties) {
    const context = useContext(Context);
    const [meta, setMeta] = useState<Meta>();
    const metaService = useMetaService();

    useEffect(() => {
        reload();
    }, []);

    const init = async () => {
        if (context.user) {
            await metaService.get(context.user.sub).then((_meta) => {
                if (_meta) {
                    context.handleChangeCompany(_meta.empresa);
                    setMeta(_meta);
                }
            });
        }
    };

    const reload = () => {
        context.startLoading();
        init()
            .finally(() => context.stopLoading())
            .catch(() => context.showDialog('noData'));
    };

    return (
        <View style={styles.container}>
            <HomeHeader type={meta?.tipoVendedor} />
            <View style={styles.body}>
                <View style={styles.navigator}>
                    <NavigationButton
                        title="Agendamento"
                        icon="schedule"
                        onPress={() => {
                            navigation.navigate('Scheduling');
                        }}
                    />
                    <NavigationButton
                        title="Carteira"
                        icon="check-box"
                        onPress={() => {
                            navigation.navigate('Wallet');
                        }}
                    />
                    <NavigationButton
                        title="Faturamento"
                        icon="bar-chart"
                        onPress={() => {
                            navigation.navigate('Billing');
                        }}
                    />
                </View>
                <View style={styles.bottom}>
                    <Container title="Apurao" onReload={reload} disabled={meta ? true : false}>
                        {meta ? (
                            <>
                                <Text style={styles.title}>
                                    ltima atualizao: {meta ? meta.dataAtualizacao : '--/--/----'} as{' '}
                                    {meta ? meta.horaAtualizacao : ' --:--'}
                                </Text>
                                <ProgressBar
                                    title="Perodo atual"
                                    step={meta.progressoPeriodo}
                                    steps={100}
                                    period={`${meta.dataInicial} a ${meta.dataFinal}`}
                                    type="date"
                                />
                                <Divider />
                                <ProgressBar
                                    title="Total Vendido"
                                    step={meta.totalVendido}
                                    steps={meta.metaValor}
                                    type="money"
                                />
                                <Divider />
                                <ProgressBar
                                    title="Prospectos"
                                    step={meta.quantidadeProspectos}
                                    steps={meta.metaProspecto}
                                    type="number"
                                />
                                <Divider />
                                <ProgressBar
                                    title="Peas vendidas"
                                    step={meta.quantidadeItens}
                                    steps={meta.metaQuantidadePecas}
                                    type="number"
                                />
                                <Divider />
                                <ProgressBar
                                    title={`Base carteira ${meta.atendimentoPercentual.toFixed(2)} %`}
                                    step={meta.quantidadeClientes}
                                    steps={meta.quantidadeCarteira}
                                    type="number"
                                />
                                <Divider />
                                <ProgressBar
                                    title="Ticket Mdio"
                                    step={meta.ticketMedio}
                                    steps={meta.ticketMedio}
                                    type="money"
                                />
                                <Divider />
                                <ProgressBar
                                    title="Reativos"
                                    step={meta.quantidadeReativos}
                                    steps={meta.quantidadeReativos}
                                    type="number"
                                />
                                <Divider />
                                <Text style={styles.subtitle}>
                                    Solicite a atualizao da apurao para seu gerente.
                                </Text>
                            </>
                        ) : (
                            <Text style={styles.subtitle}>
                                Voc ainda no possui apurao calculada, verifique com seu gerente.
                            </Text>
                        )}
                    </Container>
                </View>
            </View>
            <StatusBar style="light" translucent={false} backgroundColor="#601C5C" />
        </View>
    );
}
